<template>
  <div class="menu-wrapper">
    <template v-for="menu in menus">
      <el-submenu v-if="menu.children && menu.children.length > 0" :key="menu.key" :index="menu.key">
        <template slot="title">
          <!--<i class="iconfont icon-liebiao1"></i>-->
          <icon-svg :icon-class="menu.icon"></icon-svg>
          <span>{{menu.title}}</span>
        </template>
        <menu-item :menus="menu.children"></menu-item>
      </el-submenu>
      <el-menu-item v-else :key="menu.key" :index="menu.path">{{menu.title}}</el-menu-item>
    </template>
  </div>
</template>

<script>
export default {
  name: 'MenuItem',
  props: {
    menus: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
    }
  }
}
</script>



